import { useState, useEffect } from "react";
import style from "./css/LogOutConfirm.module.css";

function LogOutConfirm(props) {
  const [parentState, setparentState] = useState(props.isshow);
  useEffect(() => {
    setparentState(props.isshow);
  }, [props.isshow]);

  return (
    <div ref={props.LogRef} className={`${style.parent} ${style[parentState]}`}>
      <div className={style.msg}>
        <p>Are You Sure You want to log OUt</p>
      </div>
      <div className={style.cancel}>
        <input
          type="button"
          value="Cancel"
          onClick={() => {
            props.Cancelcallback();
          }}
        />
      </div>
      <div className={style.accept}>
        <input
          type="button"
          value="Accept"
          onClick={() => {
            props.Acceptcallback();
          }}
        />
      </div>
    </div>
  );
}

export default LogOutConfirm;
