import Skeleton from "react-loading-skeleton";
import React from "react";
import style from "../css/StorieSkeleton.module.css";

function storeis() {
  return (
    <section>
      <ul className={style.list}>
        {" "}
        {Array(8)
          .fill()
          .map((item, index) => (
            <li key={index} className={style.parent}>
              <Skeleton
                className={style.img}
                duration={1}
                variant="circle"
                width={55}
                height={55}
              />{" "}
              <Skeleton
                className={style.name}
                duration={1}
                height={20}
                width={`90%`}
              />{" "}
            </li>
          ))}{" "}
      </ul>{" "}
    </section>
  );
}

export default storeis;
