/* eslint-disable react/jsx-pascal-case */
import React from "react";
import style from "../css/PostSkeleton.module.css";

function post() {
  return (
    <div className={style.conatiner}>
      <ul className={style.ul_conatiner}>
        {" "}
        {Array(3)
          .fill()
          .map((item, index) => (
            <li key={index}>
              <div className={`${style.post_parent} `}>
                <div className={`${style.img} ${style.field}`}> </div>{" "}
                <div className={`${style.username} ${style.field}`}> </div>{" "}
                <div className={`${style.post_img} ${style.field}`}> </div>{" "}
              </div>{" "}
            </li>
          ))}{" "}
      </ul>{" "}
    </div>
  );
}

export default post;
